* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}


#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
}

.country-item {
  display: flex;
  align-items: center;
  gap: 10px;
}

.contry-name {
  font-weight: 500;
  font-size: 24px;
}

.country-info-img {
  display: flex;
  align-items: center;
}

.img-country {
  margin-right: 10px;
}

.country-text {
  font-weight: 700;
  font-size: 36px;
  margin: 0;
}

.info-list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 10px;
}

.info-span {
  font-weight: 700;
  font-size: 22px;
}

.info-text {
  font-weight: 500;
  font-size: 22px;
}